import React, { useMemo, useCallback, useState, useRef } from "react";
import { Container, Typography, Slider } from "@material-ui/core";
import { GamePlay } from "./GamePlay";
import { makeStyles } from "@material-ui/styles";
import { theme } from "../../assets/styles/theme";
import classnames from "classnames";
import { animated, useSpring, config } from "react-spring";

const useStyles = makeStyles({
	card1: {
		position: "relative",
		width: 200,
		top: 120,
		margin: "auto",
		padding: 10,
	},
	typo: {
		margin: 10,
		textAlign: "center",
		color: theme.colors.orange[400],
	},
	slider: {
		color: theme.colors.orange[400],
	},
	card: {
		position: "absolute",
		margin: "auto",
		top: 0,
		bottom: 0,
		right: 0,
		left: 0,
	},
	typography: {
		position: "absolute",
		alignItems: "center",
		justifyContent: "center",
		display: "flex",
		margin: 2,
		width: "100%",
		height: "100%",
	},
	noselect: {
		MozUserSelect: "none",
		WebkitUserSelect: "none",
		MoUserSelect: "none",
		UserSelect: "none",
	},
});

export const Game = () => {
	const levelRef = useRef({ size: 1 });
	const [load, setLoad] = useState(false);

	const render = useMemo(() => {
		console.log(`GAME: rendering main component`);
		return (
			<Container>
				{!load && <LevelSelect levelRef={levelRef}></LevelSelect>}
				{!load && <PlayButton onPlayClick={() => setLoad(true)}></PlayButton>}
				{load && <GamePlay level={levelRef.current.size}></GamePlay>}
			</Container>
		);
	}, [load]);

	return render;
};

const LevelSelect = ({ levelRef }) => {
	const classes = useStyles();
	const [size, setSize] = useState(1);

	const onChange = useCallback((e, value) => {
		setSize(value);
		levelRef.current.size = value;
	}, []);

	return useMemo(() => {
		console.log(`GAME: rendering card 1`);
		return (
			<div className={classes.card1}>
				<Typography variant="h4" className={classes.typo}>
					{`Level ${size}`}
				</Typography>
				<Slider
					className={classes.slider}
					step={1}
					marks
					min={1}
					max={10}
					value={size}
					onChange={onChange}
				/>
			</div>
		);
	}, [classes, onChange, size]);
};

const PlayButton = ({ onPlayClick }) => {
	const classes = useStyles();
	const [play, setPlay] = useState(false);

	const startProps = useSpring({
		config: config.stiff,
		from: { width: 0, height: 0, backgroundColor: theme.colors.orange[400], opacity: 1 },
		to: {
			width: play ? 500 : 200,
			height: play ? 500 : 80,
			opacity: play ? 0 : 1,
		},
		onRest: () => play && onPlayClick(),
	});

	const renderPlayButton = useMemo(() => {
		console.log(`GAME: rendering play button`);
		return (
			<animated.div
				className={classes.card}
				style={{ ...startProps }}
				onClick={() => setPlay(true)}
			>
				<Typography
					variant="h4"
					className={classnames(classes.typography, classes.noselect)}
				>
					"PLAY !!"
				</Typography>
			</animated.div>
		);
	}, []);

	return renderPlayButton;
};
