export const getItemsArray = (size = 9) => {
	return Array(size * size)
		.fill(0)
		.map((x, i) => {
			const c = i % size;
			const r = (i - c) / size;
			return (x = {
				id: i,
				rc: [r + 1, c + 1],
				item: i !== size * size - 1 ? i + 1 : 0,
				xy: [c * 60, r * 60],
			});
		});
};

