/**
 * https://bost.ocks.org/mike/shuffle/
 */
export function shuffle(size, matrix) {
	let array = JSON.parse(JSON.stringify(matrix));
	let m = array.length;
	let t = null;
	let i;
	// While there remain elements to shuffle…
	while (m) {
		// Pick a remaining element…
		i = Math.floor(Math.random() * m--);
		// And swap it with the current element.
		t = array[m];
		array[m] = array[i];
		array[i] = t;
	}

	// adjust xy position and rc position
	array.map((x, i) => {
		const c = i % size;
		const r = (i - c) / size;
		x.id = i;
		x.rc = [r + 1, c + 1];
		x.xy = [c * 60, r * 60];
	});

	return array;
}

export function swapItem2(array, item, destinationId) {
	const sourceId = array.findIndex((x) => x.item === item);
	const zeroId = array.findIndex((x) => x.item === 0);
	const source = array[sourceId];
	const zero = array[zeroId];
	// swapped ids
	let t = array[sourceId];
	array[sourceId] = array[zeroId];
	array[zeroId] = t;
	array.map((x, i) => {
		const c = i % 3;
		const r = (i - c) / 3;
		x.id = i;
		x.rc = [r + 1, c + 1];
		x.xy = [c * 60, r * 60];
	});
	return array;
}

export function swapItem(array, sourceId, destinationId) {
	const source = array[sourceId];
	const destination = array[destinationId];
	// swapped ids
	let s = destination;
	s.item = source.item;

	let d = source;
	d.item = destination.item;

	array[source.id] = d;
	array[destination.id] = s;
	return { returnedArray: array, sourceId: s.id, destinationId: d.id };
}

export function swapWithZero(matrix, item) {
	let array = JSON.parse(JSON.stringify(matrix));
	const sourceId = array.findIndex((x) => x.item === item);
	const zeroId = array.findIndex((x) => x.item === 0);

	let s = array[sourceId];
	array[sourceId] = array[zeroId];
	array[zeroId] = s;
	return array;
}

export const getZero = (matrix) => matrix.find((m) => m.item === 0);
export const getItem = (matrix, id) => matrix.find((m) => m.item === parseInt(id));

export const getNumberMatrix = (size = 9, isRandom = false) => {
	let numArr = getItemsArray(size);
	if (isRandom) numArr = shuffle(numArr);
	let n = 0;
	let arr = Array(size)
		.fill(0)
		.map((_, i) =>
			Array(size)
				.fill(0)
				.map((num, j) => (num = numArr[n++]))
		);
	return arr;
};
