export const GamePlay = ({ level }) => {
	const classes = useStyles();
	const size = level + 2;
	const totalCount = size * size;

	const [matrix, setMatrix] = useState(puzzleLib.getItemsArray(size));
	const [swapItems, setSwapItems] = useState(null);
	const [load, setLoad] = useState(false);

	// #region CallBacks
	const onClick = useCallback(
		(e) => {
			// const isAdjecent = puzzleLib.isAdjecent(selected, zero);
			// if (isAdjecent) {
			// Swap with zero in matrix
			var retArray = puzzleLib.swapItem2(matrix, parseInt(e.currentTarget.id));
			//zeroRef.current = destination;
			setMatrix(retArray);
			console.log(`GAMEPLAY: swaping`, retArray);
			// setZero(destinationId);
			// }
			//matrixRef.current = puzzleLib.swapWithZero(matrixRef.current, parseInt(e.currentTarget.id));
			//console.log(`GAMEPLAY: `, matrixRef.current);
		},
		[matrix]
	);

	const onShuffle = useCallback(() => {
		const shuffledArr = puzzleLib.shuffle(size, matrix);
		setMatrix(shuffledArr);
		console.log(`GAMEPLAY: Shuffled`, shuffledArr);
	}, []);

	// #endregion

	// #region react-spring animations

	const transitions = useTransition(matrix, (item) => item.item, {
		native: true,
		unique: true,
		from: ({ xy }) => ({
			transform: `translate3d(${xy[0]}px, ${xy[1]}px, 0) scale(0)`,
			width: 60,
			height: 60,
		}),
		enter: ({ xy }) => ({ transform: `translate3d(${xy[0]}, ${xy[1]}, 0) scale(1)` }),
		update: ({ xy }) => ({ transform: `translate3d(${xy[0]}px, ${xy[1]}px, 0) scale(1)` }),
		leave: { opacity: 0 },
		config: { mass: 1, tension: 200, friction: 10 },
		trail: !load ? size * size : 100,
		onRest: ({ id }) => {
			// shuffle after finishing animation on last item
			// last id is 1 less total count
			if (!load && id === totalCount - 1) {
				console.log(`GAMEPLAY: shuffling grid`);
				setLoad(true);
				onShuffle();
			}
		},
	});

	// #endregion

	// #region UseMemo

	/**
	 * Map the matrix just one time. Animations will update itself
	 */
	const render = useMemo(() => {
		console.log(`GAMEPLAY: rendering main comp`, matrix);
		return (
			<Grid
				key="grid"
				id="grid"
				container
				direction="row"
				justify="space-between"
				alignItems="flex-start"
				className={classes.card}
				style={{ width: size * 60, height: size * 60 }}
			>
				{matrix.map((m, i) => {
					const { item, props, key } = transitions[i];
					return (
						<animated.div
							key={key}
							id={key}
							style={{ position: "absolute", ...props }}
							onClick={() => {}}
						>
							<NumDiv num={item.item} id={i} />
						</animated.div>
					);
				})}
			</Grid>
		);
	}, []);

	// #endregion
	return render;
};

const NumDiv = ({ num, id }) => {
	const classes = useStyles();
	return useMemo(() => {
		console.log(`GAMEPLAY: rendering num div`);
		return (
			<Card
				key={id}
				id={id}
				className={classnames(classes.typography, classes.square, {
					[classes.transparent]: !num,
				})}
			>
				<Typography variant="h4" className={classes.noselect}>
					{num}
				</Typography>
			</Card>
		);
	}, []);
};
